import React, { useState } from "react"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import faEnvelope from "@fortawesome/fontawesome-free-solid/faEnvelope"
import { Form } from "react-bulma-components/lib"
import Icon from "react-bulma-components/lib/components/icon"
import Button from "react-bulma-components/lib/components/button"
import Notification from "react-bulma-components/lib/components/notification"

const NewsletterForm = () => {
  const [state] = useState({ sended: false })
  const { values, handleChange, handleSubmit } = useForm(send)

  async function send() {
    const response = await fetch("http://localhost:3000?email=" + values.email)
    const data = await response.json()
    if (data.success === true) {
      state.sended = true
    }
  }

  const attributes = values.loading ? { loading: true } : {}

  return (
    <>
      {state.sended === false ? (
        <Form.Field kind="addons">
          <Form.Control iconLeft fullwidth>
            <Form.Input
              color="success"
              type="email"
              name="email"
              placeholder="Psst, trag Dich ein diggi..."
              onChange={handleChange}
              value={values.email}
              required
            />
            <Icon align="left">
              <FontAwesomeIcon align="left" icon={faEnvelope} />
            </Icon>
          </Form.Control>
          <Form.Control iconLeft>
            <Button
              type="submit"
              color="success"
              onClick={handleSubmit}
              {...attributes}
            >
              Trag mich ein
            </Button>
          </Form.Control>
        </Form.Field>
      ) : (
        <Notification color="success">
          Cool, {values.email} wurde eingetragen! Du bekommst nun eine Email zur
          Bestätigung.
        </Notification>
      )}
    </>
  )
}

const useForm = callback => {
  const [values, setValues] = useState({ loading: false })

  const handleSubmit = event => {
    if (event) event.preventDefault()
    setValues(values => ({ ...values, loading: true }))
    callback()
  }

  const handleChange = event => {
    event.persist()
    setValues(values => ({
      ...values,
      [event.target.name]: event.target.value,
    }))
  }

  return {
    handleChange,
    handleSubmit,
    values,
  }
}

export default NewsletterForm
