import React, { useState } from "react"
import Button from "react-bulma-components/lib/components/button"
import Notification from "react-bulma-components/lib/components/notification"
import Tag from "react-bulma-components/lib/components/tag"

const ConsentContainer = ({ children }) => {
  const [state, setState] = useState({ clicked: false })
  const consent = () => {
    setState(true)
  }
  return (
    <>
      {state.clicked === false ? (
        <Notification className={"is-size-7"}>
          <Button
            color="success"
            className="is-large is-fullwidth"
            onClick={consent}
            style={{ marginBottom: "1rem" }}
          >
            Yeah, ich will auch den Newsletter!
          </Button>
          Das Newsletter Widget lädt Daten von Mailjet, Cloudflare und Google
          Fonts. <Tag color="warning">DSGVO</Tag>
        </Notification>
      ) : (
        { ...children }
      )}
    </>
  )
}

export default ConsentContainer
